<template>
  <header class="bg-[#032541] h-16 flex items-center px-12 relative">
    <img class="h-6 mr-4" :src="logoUrl" alt="Logo"/>
    <nav class="flex-grow">
      <router-link to="/movies" class="text-white hover:underline mx-4">Movies</router-link>
      <router-link to="/tv-shows" class="text-white hover:underline">TV Shows</router-link>
    </nav>
    <button @click="toggleSearch" class="focus:outline-none ml-4 w-8">
      <img v-if="!isSearchVisible" src="https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-28-search-blue-177462d06db81ff2a02aa022c1c0be5ba4200d7bd3f51091ed9298980e3a26a1.svg" alt="Search Icon"/>
      <img v-else src="https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-599-menu-close-7400e9a2ea92ad8d7cccf18d1ea34cd690790638a6e7768922eaef6e07109723.svg" alt="Close Icon"/>
    </button>
    <div v-if="isSearchVisible" class="absolute top-16 left-0 w-full bg-white shadow-lg p-4 z-20">
      <input
        v-model="searchQuery"
        @input="debounceSearch"
        type="text"
        placeholder="Search for a movie, tv show, person..."
        class="w-full h-[46px] border rounded-full px-4"
      />
      <ul class="mt-2 border-t border-gray-300">
        <li v-for="movie in suggestedMovies" :key="movie.id" class="border-b py-2">{{ movie.title }}</li>
      </ul>
    </div>
  </header>
</template>

<script>
import _ from 'lodash';

export default {
  name: 'HeaderComponent',
  data() {
    return {
      logoUrl: 'https://www.themoviedb.org/assets/2/v4/logos/v2/blue_short-8e7b30f73a4020692ccca9c88bafe5dcb6f8a62a4c6bc55cd9ba82bb2cd95f6c.svg',
      isSearchVisible: false,
      searchQuery: '',
      suggestedMovies: []
    }
  },
  methods: {
    toggleSearch() {
      this.isSearchVisible = !this.isSearchVisible;
      if (!this.isSearchVisible) {
        this.searchQuery = '';
        this.suggestedMovies = [];
      } else {
        this.fetchSuggestedMovies();
      }
    },
    async fetchSuggestedMovies() {
      const url = `https://api.themoviedb.org/3/movie/popular?api_key=348088421ad3fb3a9d6e56bb6a9a8f80`;
      const response = await fetch(url);
      const data = await response.json();
      this.suggestedMovies = data.results.slice(0, 10);
    },
    debounceSearch: _.debounce(async function () {
      if (this.searchQuery.length >= 3) {
        const url = `https://api.themoviedb.org/3/search/movie?api_key=348088421ad3fb3a9d6e56bb6a9a8f80&query=${encodeURIComponent(this.searchQuery)}`;
        const response = await fetch(url);
        const data = await response.json();
        this.suggestedMovies = data.results ? data.results.slice(0, 10) : [];
      } else {
        this.suggestedMovies = [];
      }
    }, 500),
  }
}
</script>

